/*@headerTextColor: #2d9cd0;*/
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  /*margin-top: 60px;*/
}
body {
  margin: 0;
  color: #333;
}
a {
  color: #444;
}
a:hover {
  background-color: #9d0006;
  color: #fff;
}
.have-no-content {
  padding-top: 20px;
  text-align: center;
  color: #aaa;
  font-size: 12px;
}
header {
  margin: 0;
  width: 100%;
  background-color: #eee;
  /*height: 50px;*/
}
header .header-wrapper {
  overflow: hidden;
  margin: 0 auto;
  padding: 4px;
  width: 100%;
  max-width: 800px;
}
header .header-wrapper .blog-title {
  float: left;
  margin: 0;
  font-size: 12px;
  color: #444;
}
header .header-wrapper nav {
  float: right;
  overflow: hidden;
  margin: 0 auto;
}
header .header-wrapper nav a {
  margin-left: 7px;
  font-size: 12px;
  text-decoration: none;
}
header .header-wrapper nav .nav-active {
  text-decoration: underline;
}
.postBodyStyle p,
.postBodyStyle li,
.postBodyStyle a,
.postBodyStyle div,
.postBodyStyle pre,
.postBodyStyle code {
  font-size: 14px;
}
.postBodyStyle p,
.postBodyStyle li,
.postBodyStyle div {
  color: #333;
  line-height: 1.7;
}
.postBodyStyle a {
  text-decoration: underline;
  color: #9d0006;
}
.postBodyStyle a:hover {
  background-color: #9d0006;
  color: #fff;
}
.page {
  margin: 0 auto;
  padding: 30px 0;
  width: 100%;
  max-width: 800px;
}
.page .loading {
  text-align: center;
  font-size: 12px;
}
.page .post-title {
  margin: 10px 0;
}
.page .post-title a {
  text-decoration: none;
  color: #9d0006;
}
.page .post-title a:hover {
  color: #fff;
}
.page .post-time {
  font-size: 12px;
  color: #666;
}
.page .post-body {
  padding-top: 25px;
}
.page .post-body p,
.page .post-body li,
.page .post-body a,
.page .post-body div,
.page .post-body pre,
.page .post-body code {
  font-size: 14px;
}
.page .post-body p,
.page .post-body li,
.page .post-body div {
  color: #333;
  line-height: 1.7;
}
.page .post-body a {
  text-decoration: underline;
  color: #9d0006;
}
.page .post-body a:hover {
  background-color: #9d0006;
  color: #fff;
}
.page .archive {
  list-style: none;
}
.page .archive .archive-year-title {
  color: #9d0006;
}
.page .archive .archive-month {
  list-style: none;
  padding-left: 5px;
}
.page .archive .archive-month .archive-month-title {
  font-size: 18px;
}
.page .archive .archive-posts {
  line-height: 150%;
}
.page .archive .archive-posts a {
  text-decoration: none;
}
footer .footer-content {
  padding: 20px 0;
  color: #444;
  font-size: 12px;
  text-align: center;
}













































































































































